import { PageTitle } from '../../components/common/Typography';
import { Wrapper, Container, P, Subtitle } from './Policy.styled';

const PrivacyPage = () => {
  return (
    <Wrapper className="page">
      <Container>
        <PageTitle textAlign="center">PRIVACY</PageTitle>
        <Subtitle>Updated: August 15th, 2022</Subtitle>
        <P>
          The $MD Token website is a web-based interface application accessible at
          https://MDtoken.io (also referred to as the “Platform”) operated exclusively by MDToken.
          <br />
          <br />
          This Privacy Policy sets out the terms of processing personal data within the Platform.
          <br />
          <ol>
            <li>
              PERSONAL DATA THAT MDToken PROCESSES. In order to enable you to use the Platform,
              MDToken will receive your public Cardano blockchain address (starting with “addr1”,
              the “Address”), and information about your transactions on the blockchain with the
              Address (“Transactions”), which, in certain circumstances, may be considered personal
              data. MDToken collects and processes your address when you connect your digital wallet
              to the Platform. When you make a Transaction to the Platform, we process information
              about such Transactions.
            </li>
            <li>
              HOW MDToken USES YOUR PERSONAL DATA. We use the Address and information about
              Transactions to enable you to use the Platform. The legal basis for processing your
              Address and information about your Transactions is the necessity for the performance
              of a contract between you and MDToken. The only mandatory personal data required for
              use within the Platform is already publicly available on the Cardano blockchain. In
              some cases, when you choose to interact with third-party software, services or tools,
              your Address or certain information regarding your Transactions may be transferred to
              such third-party software, services or tools, respectively.
            </li>
            <li>
              PERSONAL DATA AND BLOCKCHAIN. Please kindly note that your Address and information on
              your Transactions will be used within the Cardano blockchain, which is out of
              MDToken&apos; control. This means that due to the structure of the Cardano blockchain,
              certain data protection rights or abilities may be limited. It also means that your
              Address and information regarding your Transactions may be freely accessed by any
              person who has access to the Cardano blockchain. Please learn more about personal data
              and the blockchain on the official Cardano webpage, https://iohk.io.
            </li>
            <li>
              TRANSACTIONS ON THE PLATFORM. All transactions that you make on the Platform will be
              processed and validated by MDToken and then submitted onto the Cardano blockchain.
            </li>
          </ol>
        </P>
      </Container>
    </Wrapper>
  );
};

export default PrivacyPage;
