import styled from 'styled-components';

export const Wrapper = styled.div`
  padding: 175px 0;
  @media screen and (max-width: 991px) {
    padding: 142px 0;
  }
`;

export const Container = styled.div`
  max-width: 1160px;
  padding-top: 120px;
  margin: 0 auto;

  h6 {
    letter-spacing: 3px;
  }

  @media screen and (max-width: 991px) {
    padding: 0 38px;
  }
  @media screen and (max-width: 550px) {
    padding: 0 20px;
  }
`;

export const P = styled.p`
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.05em;
  @media screen and (max-width: 550px) {
    font-size: 14px;
  }
`;

export const Subtitle = styled.div`
  font-size: 40px;
  padding-top: 10px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 60px;
  @media screen and (max-width: 425px) {
    font-size: 18px;
    margin-top: -32px;
    padding-top: 0;
  }
`;
